<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 等级编码 -->
            <a-form-model-item
              label="等级编码"
              prop="levelCode"
              ref="levelCode"
            >
              <a-input
                v-model="entity.levelCode"
                placeholder="请输入等级编码，格式为BL开头加4位数字"
                name="levelCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 等级名称 -->
            <a-form-model-item
              label="等级名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入等级名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 等级类型 -->
            <a-form-model-item
              label="等级类型"
              prop="levelType"
              ref="levelType"
            >
              <a-select
                v-model="entity.levelType"
                placeholder="请选择等级类型"
                name="levelType"
              >
                <a-select-option value="质量等级">质量等级</a-select-option>
                <a-select-option value="工艺等级">工艺等级</a-select-option>
                <a-select-option value="性能等级">性能等级</a-select-option>
                <a-select-option value="安全等级">安全等级</a-select-option>
                <a-select-option value="环保等级">环保等级</a-select-option>
                <a-select-option value="其他等级">其他等级</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 等级代码 -->
            <a-form-model-item
              label="等级代码"
              prop="levelCode"
              ref="levelCode"
            >
              <a-input
                v-model="entity.levelCode"
                placeholder="请输入等级代码"
                name="levelCode"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status" name="status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 等级描述 -->
            <a-form-model-item 
              label="等级描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述"
                :rows="2"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 等级标准 -->
      <g-collapse headerName="等级标准">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-standard-btn" type="primary" icon="plus" @click="addStandard">添加标准</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="standardColumns" 
                :dataSource="entity.standards" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="standardName" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].standardName" placeholder="请输入标准名称" />
                </template>
                <template slot="standardValue" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].standardValue" placeholder="请输入标准值" />
                </template>
                <template slot="standardUnit" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].standardUnit" placeholder="请输入标准单位" />
                </template>
                <template slot="note" slot-scope="text, record, index">
                  <a-input v-model="entity.standards[index].note" placeholder="请输入说明" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeStandard(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditBatchLevel",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        levelCode: "",
        name: "",
        levelType: "",
        levelCode: "",
        description: "",
        standards: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      rules: {
        levelCode: [
          { required: true, message: "请输入等级编码", trigger: "change" },
          { pattern: /^BL\d{4}$/, message: "等级编码必须为BL开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入等级名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "等级名称不能包含前后空格", trigger: "change" }
        ],
        levelType: [
          { required: true, message: "请选择等级类型", trigger: "change" }
        ],
        levelCode: [
          { required: true, message: "请输入等级代码", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      standardColumns: [
        {
          title: "标准名称",
          dataIndex: "standardName",
          key: "standardName",
          width: "25%",
          scopedSlots: { customRender: "standardName" }
        },
        {
          title: "标准值",
          dataIndex: "standardValue",
          key: "standardValue",
          width: "25%",
          scopedSlots: { customRender: "standardValue" }
        },
        {
          title: "标准单位",
          dataIndex: "standardUnit",
          key: "standardUnit",
          width: "20%",
          scopedSlots: { customRender: "standardUnit" }
        },
        {
          title: "说明",
          dataIndex: "note",
          key: "note",
          width: "20%",
          scopedSlots: { customRender: "note" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取等级数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        levelCode: "",
        name: "",
        levelType: "",
        levelCode: "",
        description: "",
        standards: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          // 确保standards数组被正确初始化
          standards: row.standards || []
        };
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 添加等级标准
    addStandard() {
      this.entity.standards.push({
        id: Date.now(), // 使用时间戳作为临时ID
        standardName: "",
        standardValue: "",
        standardUnit: "",
        note: ""
      });
    },
    
    // 删除等级标准
    removeStandard(index) {
      this.entity.standards.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-standard-btn {
  margin-bottom: 16px;
}
</style> 